.TodoInsert {
    display: flex;
    background: rgb(179, 179, 179);
    input {
        background: none;
        outline: none;
        border: none;
        padding: 0.5rem;
        font-size: 1.125rem;
        line-height: 1.5;
        color: white;
        &::placeholder {
            color: rgb(224, 224, 224);
        }
        flex: 1;
    }
    button {
        background: none;
        outline: none;
        border: none;
        background: rgb(141, 141, 141);
        color: white;
        padding-left: 1rem;
        padding-right: 1rem;
        font-size: 1.5rem;
        display: flex;
        align-items: center;
        cursor: pointer;
        transition: 0.1s ease-in;
        &:hover {
            background: rgb(247, 169, 160);
        }
    }
}